import React, { useState, useEffect, Fragment } from 'react';

import UsersList from '../components/UsersList';
import ErrorModal from '../../shared/components/UIElmenets/ErrorModal/ErrorModal';
import LoadingSpinner from '../../shared/components/UIElmenets/LoadingSpinner/LoadingSpinner';

import { useHttpClient } from '../../shared/hooks/useHttp/useHttp';

const Users = () => {
    const { isLoading, error, sendReq, clearError } = useHttpClient();
    const [loadedUsers, setLoadedUsers] = useState();

    useEffect(() => {
        const fetchUsers = async () => {
            try {
                const resData = await sendReq(process.env.REACT_APP_BACKEND_URL + '/users');

                setLoadedUsers(resData.users);
            } catch (err) {}
        };

        fetchUsers();
    }, [sendReq]);

    return (
        <Fragment>
            <ErrorModal error={error} onClear={clearError} />
            {isLoading && (
                <div className='center'>
                    <LoadingSpinner />
                </div>
            )}
            {!isLoading && loadedUsers && <UsersList items={loadedUsers} />}
        </Fragment>
    );
};

export default Users;
